h1 {
  text-align: center;
  display: block;
  font-size: 25px;
}

.for-print {
  text-align: center;
  display: block;
  font-size: 22px;
  font-weight: 600;
  margin: 25px 0;
}

.text {
  font-size: 16px;
}

.text .string {
  border-bottom: 1px solid #eee;
  padding: 5px 0;
  display: flex;
}

.text .string.title {
  text-align: center;
  font-weight: 600;
  font-size: 20px;
}

.text .string .original {
  width: 50%;
  padding: 0 10px;
  border-right: 1px solid #eee;
}

.text .string .translate {
  width: 50%;
  padding: 0 10px;
}

.text .string .translate {
  font-weight: 600;
}
